<template>
  <div class="bg-white">
    <section class="text-white bg-white">
         <!-- <figure>
            <img src="@/assets/img/singlepole-top-blue.png" alt="" />
         </figure> -->

         <div class="flex flex-col pb-12 gap-y-6 bg-blue">
            <h2 class="pt-6 text-3xl text-white md:text-6xl lg:text-4xl">- Enviar solicitud de reserva -</h2>
            <span class="text-5xl font-Clarissa">{{ title }}</span>

            <div class="container p-5 mx-auto border-2 border-white border-dashed rounded">
              <!-- REQUEST INFO  -->
              
              <general-request :request="request_info" @updInfo="updRequest"></general-request>              
              
              <form class="flex flex-col flex-wrap justify-between flex-1 px-10 py-8 text-xl gap-x-3 gap-y-6 shadow-round bg-darkBlue md:flex-row">                
                <input 
                type="text" 
                placeholder="Nombre completo" 
                v-model="name" 
                min="0" 
                class="w-full px-6 py-2 text-left border rounded-4xl text-orange bg-darkBlue md:w-1/2">

                <input 
                type="text" 
                placeholder="Teléfono" 
                v-model="phone" 
                min="0" 
                class="w-full px-6 py-2 text-left border rounded-4xl text-orange bg-darkBlue md:w-1/5">

                <input 
                type="email" 
                placeholder="Email*" 
                v-model="email" 
                required
                min="0" 
                class="w-full px-6 py-2 text-left border rounded-4xl text-orange bg-darkBlue md:w-1/4">

                <textarea id="" 
                placeholder="Descripcion de su solicitud"
                v-model="message" 
                class="w-full h-40 px-8 py-6 text-left border rounded-4xl text-orange bg-darkBlue"
                ></textarea>

                <div class="flex items-center w-full gap-3">  
                  <span class="items-center inline px-2 py-1 border cursor-pointer rounded-4xl bg-darkBlue"
                  @click="activeTerm = !activeTerm"
                  :class="isActiveTerm">
                    <i class="fas fa-check"></i>
                  </span>
                  <label class="self-center text-orange"
                  @click="activeTerm = !activeTerm"
                  >Términos y condiciones</label>
                </div>

                <div class="flex flex-col w-full gap-3 md:flex-row">
                  <span class="p-2 px-4 text-left bg-red rounded-xl md:w-3/5" v-show="error">{{ error_message }}</span>
                  <span class="p-2 px-4 text-left bg-green-500 rounded-xl md:w-3/5" v-show="success">La solicitud fue enviada exitosamente!!!</span>
                  <button type="button" class="px-12 py-1 text-base text-white uppercase border md:text-xl bg-orange border-orange rounded-4xl md:ml-auto hover:bg-darkBlue hover:text-orange"
                  @click="formSubmit">Enviar Solicitud</button>
                </div>
              </form>
            </div>
         </div>         
    </section>
  </div>
</template>
<script>
import GeneralRequest from '@/components/request/general_request'
import Authorization from "@/services/user/auth.service";
// import moment from 'moment'

export default {  
  name: "ContactoComp",
  props: {
    title: {
      require: false,
      default: 'Solicitud'
    },    
  },  
  components: {
    GeneralRequest     
  },
  data() {
    return {      
      activeTerm: false,
      is_hotel: true,   
      success: false,   
      error_message: 'Error. No se pudo enviar la solicitud!!!',   
      error: false,               
      request_info: {type: 'home', handler: {}},
      name: '',
      phone: '',
      email: '',      
      message: '',
      subject: '',
      msg_info: {}

    }
  },  
  created() {
    // iniciando los datos del pedido
    const info = localStorage.getItem('request_info')
    console.log(JSON.parse(info))
    this.request_info = (info) && JSON.parse(info)
    // this.request = (info) ? JSON.parse(info) : this.request
    if(this.request_inf != undefined) {
        this.subject = this.request_info.product
      }
  },  
  unmounted() {
    localStorage.removeItem('request_info')
  },
  watch: {
    dateRange(d) {
      this.request.date = d.startDate
    }
  },
  computed: {
    isActiveTerm() {
      return (this.activeTerm) ? 'text-orange' : 'text-darkBlue'
    }
  },
  methods: {
    updPerson(obj){
        this.request.num_person = obj.val
    },
    async formSubmit() {      
      const body = {
        subject: this.subject,
        mail: this.email,
        message: {
          name: this.name,
          phone: this.phone,
          email: this.email,
          message: this.message,
          msg_info: this.msg_info
        },
      }            

      if(this.validEmail(this.email)) {        
        if(await Authorization.checkLogin('/send-request', {body, auth: 'bearer'}, 'post')) {
          localStorage.removeItem('request_info')
          this.success = true 
          setTimeout(() => {
              this.success = false
          }, 3000); 
        } else {
          this.error = true 
          setTimeout(() => {
              this.error = false
          }, 3000);          
        }
      } else {
        this.error = true 
        this.error_message = 'Debe insertar una dirección de correo válida.'
          setTimeout(() => {
              this.error_message = 'Error. No se pudo enviar la solicitud!!!'
              this.error = false
          }, 3000);          
      }
    },
    validEmail(mail) {
      if(mail) {
        if(mail.match(/^[\w-.]+@([\w-]+\.)+[\w-]{2,8}$/))
          return true
      }
      return false
    },
    updRequest(msg) {
      this.msg_info = msg
    }
  }
};
</script>
